import React from 'react';
import { Box, Typography, Button, useTheme, useMediaQuery } from '@mui/material';

const PropertyPreview: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  // Placeholder image URL (replace with actual property image)
  const placeholderImage = 'https://images.unsplash.com/photo-1564013799919-ab600027ffc6?auto=format&fit=crop&w=1200&q=80';

  return (
    <Box sx={{ 
      p: isMobile ? 2 : 3,
      border: '1px solid rgba(0,0,0,0.1)',
      borderRadius: '12px',
      backgroundColor: 'background.paper'
    }}>
      <Box sx={{ 
        width: '100%',
        height: isMobile ? '200px' : '300px',
        borderRadius: '8px',
        overflow: 'hidden',
        mb: 2
      }}>
        <img 
          src={placeholderImage}
          alt="Property Preview" 
          style={{ 
            width: '100%',
            height: '100%',
            objectFit: 'cover'
          }}
        />
      </Box>

      <Box sx={{ 
        display: 'grid',
        gridTemplateColumns: isMobile ? '1fr' : 'repeat(2, 1fr)',
        gap: 2,
        mb: 2
      }}>
        <Box>
          <Typography 
            variant="body2" 
            color="text.secondary"
            sx={{ fontSize: isMobile ? '0.75rem' : '0.875rem' }}
          >
            Address:
          </Typography>
          <Typography 
            variant="body1"
            sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
          >
            123 Main St 10101
          </Typography>
        </Box>

        <Box>
          <Typography 
            variant="body2" 
            color="text.secondary"
            sx={{ fontSize: isMobile ? '0.75rem' : '0.875rem' }}
          >
            Name:
          </Typography>
          <Typography 
            variant="body1"
            sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
          >
            Mike Towers
          </Typography>
        </Box>

        <Box>
          <Typography 
            variant="body2" 
            color="text.secondary"
            sx={{ fontSize: isMobile ? '0.75rem' : '0.875rem' }}
          >
            State:
          </Typography>
          <Typography 
            variant="body1"
            sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
          >
            New York
          </Typography>
        </Box>

        <Box>
          <Typography 
            variant="body2" 
            color="text.secondary"
            sx={{ fontSize: isMobile ? '0.75rem' : '0.875rem' }}
          >
            Number:
          </Typography>
          <Typography 
            variant="body1"
            sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
          >
            (555) 444-1111
          </Typography>
        </Box>
      </Box>

      <Box sx={{ 
        display: 'flex',
        flexDirection: isMobile ? 'column' : 'row',
        gap: 2
      }}>
        <Button
          variant="outlined"
          color="primary"
          fullWidth={isMobile}
          sx={{ 
            borderRadius: '8px',
            textTransform: 'none',
            fontWeight: 600,
            fontSize: isMobile ? '0.875rem' : '1rem',
            display: 'flex',
            alignItems: 'center',
            gap: 1,
            color: '#006AFF',
            borderColor: '#006AFF',
            '&:hover': {
              borderColor: '#005AE0',
              backgroundColor: 'rgba(0, 106, 255, 0.04)'
            }
          }}
        >
          <svg width="20" height="20" viewBox="0 0 1000 1000" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M500 0C223.858 0 0 223.858 0 500C0 776.142 223.858 1000 500 1000C776.142 1000 1000 776.142 1000 500C1000 223.858 776.142 0 500 0Z" fill="#006AFF"/>
            <path d="M731.438 451.362L574.562 294.486C553.75 273.674 519.375 273.674 498.562 294.486L341.686 451.362C320.874 472.174 320.874 506.549 341.686 527.362L498.562 684.238C519.374 705.05 553.749 705.05 574.562 684.238L731.438 527.362C752.25 506.549 752.25 472.174 731.438 451.362Z" fill="white"/>
          </svg>
          Zillow
        </Button>
        <Button
          variant="outlined"
          color="error"
          fullWidth={isMobile}
          sx={{ 
            borderRadius: '8px',
            textTransform: 'none',
            fontWeight: 600,
            fontSize: isMobile ? '0.875rem' : '1rem',
            display: 'flex',
            alignItems: 'center',
            gap: 1,
            color: '#E02D2D',
            borderColor: '#E02D2D',
            '&:hover': {
              borderColor: '#C42828',
              backgroundColor: 'rgba(224, 45, 45, 0.04)'
            }
          }}
        >
          <svg width="20" height="20" viewBox="0 0 1000 1000" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M500 0C223.858 0 0 223.858 0 500C0 776.142 223.858 1000 500 1000C776.142 1000 1000 776.142 1000 500C1000 223.858 776.142 0 500 0Z" fill="#E02D2D"/>
            <path d="M707.107 292.893C687.5 273.286 656.066 273.286 636.46 292.893L500 429.353L363.54 292.893C343.934 273.286 312.5 273.286 292.893 292.893C273.286 312.5 273.286 343.934 292.893 363.54L429.353 500L292.893 636.46C273.286 656.066 273.286 687.5 292.893 707.107C312.5 726.714 343.934 726.714 363.54 707.107L500 570.647L636.46 707.107C656.066 726.714 687.5 726.714 707.107 707.107C726.714 687.5 726.714 656.066 707.107 636.46L570.647 500L707.107 363.54C726.714 343.934 726.714 312.5 707.107 292.893Z" fill="white"/>
          </svg>
          Redfin
        </Button>
      </Box>
    </Box>
  );
};

export default PropertyPreview;
