import React, { useState } from 'react';
import { Box, Button, Typography, LinearProgress, TextField, useTheme, useMediaQuery } from '@mui/material';
import PracticeMode from './components/PracticeMode';
import Logo from './components/Logo';
import PropertyPreview from './components/PropertyPreview';

const App: React.FC = () => {
  const [isPracticeMode, setIsPracticeMode] = useState(false);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const isTablet = useMediaQuery(theme.breakpoints.down('md'));

  const handleEnterPracticeMode = () => {
    setIsPracticeMode(true);
  };

  const handleExitPracticeMode = () => {
    setIsPracticeMode(false);
  };

  const progressBarStyle = {
    height: 4,
    borderRadius: 2,
    '& .MuiLinearProgress-bar': {
      borderRadius: 2
    }
  };

  if (isPracticeMode) {
    return <PracticeMode onExitPracticeMode={handleExitPracticeMode} />;
  }

  return (
    <Box>
      {/* Header */}
      <Box sx={{ 
        display: 'flex', 
        flexDirection: isMobile ? 'column' : 'row',
        justifyContent: 'space-between', 
        alignItems: isMobile ? 'stretch' : 'center',
        px: isMobile ? 2 : 4,
        py: 2,
        gap: isMobile ? 2 : 0,
        bgcolor: '#2c3e50'
      }}>
        <Box sx={{ 
          display: 'flex', 
          alignItems: 'center',
          justifyContent: isMobile ? 'center' : 'flex-start'
        }}>
          <Logo />
        </Box>
        <Box sx={{ 
          display: 'flex', 
          gap: 2,
          flexDirection: isMobile ? 'column' : 'row'
        }}>
          <Button 
            variant="contained" 
            color="success"
            fullWidth={isMobile}
            sx={{ 
              borderRadius: '8px',
              textTransform: 'none',
              fontWeight: 600,
              background: '#27ae60',
              '&:hover': {
                background: '#219a52'
              },
              fontSize: isMobile ? '0.9rem' : '1rem'
            }}
          >
            📞 Phone Dialer
          </Button>
          <Button 
            variant="contained" 
            color="primary"
            fullWidth={isMobile}
            onClick={handleEnterPracticeMode}
            sx={{ 
              borderRadius: '8px',
              textTransform: 'none',
              fontWeight: 600,
              background: '#3498db',
              '&:hover': {
                background: '#2980b9'
              },
              fontSize: isMobile ? '0.9rem' : '1rem'
            }}
          >
            👨‍🏫 Enter Practice Mode
          </Button>
        </Box>
      </Box>

      {/* Main Content */}
      <Box sx={{ p: isMobile ? 2 : 4 }}>
        <Box sx={{ 
          display: 'flex', 
          flexDirection: isMobile ? 'column' : 'row',
          justifyContent: 'center',
          alignItems: 'center',
          gap: 2,
          mb: 3
        }}>
          <Button 
            variant="outlined" 
            color="primary"
            fullWidth={isMobile}
            sx={{ 
              borderRadius: '8px',
              textTransform: 'none',
              fontWeight: 600,
              px: 4,
              fontSize: isMobile ? '0.9rem' : '1rem'
            }}
          >
            📄 Custom Script
          </Button>
          <Button 
            variant="outlined" 
            color="primary"
            fullWidth={isMobile}
            sx={{ 
              borderRadius: '8px',
              textTransform: 'none',
              fontWeight: 600,
              px: 4,
              fontSize: isMobile ? '0.9rem' : '1rem'
            }}
          >
            🔄 Hybrid Mode
          </Button>
          <Button 
            variant="outlined" 
            color="primary"
            fullWidth={isMobile}
            sx={{ 
              borderRadius: '8px',
              textTransform: 'none',
              fontWeight: 600,
              px: 4,
              fontSize: isMobile ? '0.9rem' : '1rem'
            }}
          >
            🤖 AI Script
          </Button>
        </Box>

        <Typography 
          variant="body2" 
          color="text.secondary" 
          align="center" 
          sx={{ 
            mb: 3,
            fontSize: isMobile ? '0.875rem' : '1rem'
          }}
        >
          Status: Ready
        </Typography>

        <Box sx={{ 
          display: 'flex', 
          flexDirection: isTablet ? 'column' : 'row',
          gap: isMobile ? 2 : 4 
        }}>
          {/* Left Panel */}
          <Box sx={{ flex: 1 }}>
            <Box sx={{ 
              p: isMobile ? 2 : 3,
              border: '1px solid rgba(0,0,0,0.1)',
              borderRadius: '12px',
              backgroundColor: 'background.paper',
              mb: isMobile ? 2 : 3
            }}>
              <Typography 
                variant="h6" 
                gutterBottom
                sx={{ fontSize: isMobile ? '1.1rem' : '1.25rem' }}
              >
                Live Transcription
              </Typography>
              <Typography 
                variant="body2" 
                color="text.secondary"
                sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
              >
                Press spacebar to start practice
              </Typography>
            </Box>

            <PropertyPreview />

            <Box sx={{ 
              mt: isMobile ? 2 : 3,
              p: isMobile ? 2 : 3,
              border: '1px solid rgba(0,0,0,0.1)',
              borderRadius: '12px',
              backgroundColor: 'background.paper'
            }}>
              <Typography 
                variant="h6" 
                gutterBottom
                sx={{ fontSize: isMobile ? '1.1rem' : '1.25rem' }}
              >
                Speaking Metrics
              </Typography>
              <Box sx={{ mb: 2 }}>
                <Typography 
                  variant="subtitle2" 
                  gutterBottom
                  sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                >
                  Speaking Ratio (You/Client)
                </Typography>
                <Typography 
                  variant="h6"
                  sx={{ fontSize: isMobile ? '1.1rem' : '1.25rem' }}
                >
                  0% / 0%
                </Typography>
              </Box>
              <Box>
                <Typography 
                  variant="subtitle2" 
                  gutterBottom
                  sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                >
                  Call Duration
                </Typography>
                <Typography 
                  variant="h6"
                  sx={{ fontSize: isMobile ? '1.1rem' : '1.25rem' }}
                >
                  00:00
                </Typography>
              </Box>
            </Box>
          </Box>

          {/* Right Panel */}
          <Box sx={{ flex: 1 }}>
            <Box sx={{ 
              p: isMobile ? 2 : 3,
              border: '1px solid rgba(0,0,0,0.1)',
              borderRadius: '12px',
              backgroundColor: 'background.paper',
              mb: isMobile ? 2 : 3
            }}>
              <Typography 
                variant="h6" 
                gutterBottom
                sx={{ fontSize: isMobile ? '1.1rem' : '1.25rem' }}
              >
                Conversation Pillars
              </Typography>
              <Box sx={{ mb: 3 }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 0.5, alignItems: 'center' }}>
                  <Typography 
                    variant="body2"
                    sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                  >
                    Vacancy
                  </Typography>
                  <Typography 
                    variant="body2"
                    sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                  >
                    45%
                  </Typography>
                </Box>
                <LinearProgress variant="determinate" value={45} color="success" sx={{ ...progressBarStyle, mb: 2 }} />

                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 0.5, alignItems: 'center' }}>
                  <Typography 
                    variant="body2"
                    sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                  >
                    Condition
                  </Typography>
                  <Typography 
                    variant="body2"
                    sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                  >
                    36%
                  </Typography>
                </Box>
                <LinearProgress variant="determinate" value={36} color="info" sx={{ ...progressBarStyle, mb: 2 }} />

                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 0.5, alignItems: 'center' }}>
                  <Typography 
                    variant="body2"
                    sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                  >
                    Motivation
                  </Typography>
                  <Typography 
                    variant="body2"
                    sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                  >
                    38%
                  </Typography>
                </Box>
                <LinearProgress variant="determinate" value={38} color="warning" sx={{ ...progressBarStyle, mb: 2 }} />

                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 0.5, alignItems: 'center' }}>
                  <Typography 
                    variant="body2"
                    sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                  >
                    Price
                  </Typography>
                  <Typography 
                    variant="body2"
                    sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                  >
                    52%
                  </Typography>
                </Box>
                <LinearProgress variant="determinate" value={52} color="secondary" sx={{ ...progressBarStyle, mb: 2 }} />

                <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 0.5, alignItems: 'center' }}>
                  <Typography 
                    variant="body2"
                    sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                  >
                    Timeframe
                  </Typography>
                  <Typography 
                    variant="body2"
                    sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
                  >
                    36%
                  </Typography>
                </Box>
                <LinearProgress variant="determinate" value={36} sx={{ ...progressBarStyle }} />
              </Box>

              <Typography 
                variant="h6" 
                gutterBottom
                sx={{ fontSize: isMobile ? '1.1rem' : '1.25rem' }}
              >
                Quick Notes
              </Typography>
              <TextField
                fullWidth
                placeholder="Add a quick note..."
                variant="outlined"
                size={isMobile ? "small" : "medium"}
                sx={{ 
                  mb: 2,
                  '& .MuiInputBase-input': {
                    fontSize: isMobile ? '0.875rem' : '1rem'
                  }
                }}
              />
              <Typography 
                variant="body2" 
                color="text.secondary" 
                align="center"
                sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
              >
                No notes yet
              </Typography>
            </Box>

            {/* Script Generator */}
            <Box sx={{ 
              p: isMobile ? 2 : 3,
              border: '1px solid rgba(0,0,0,0.1)',
              borderRadius: '12px',
              backgroundColor: 'background.paper',
              minHeight: isMobile ? 100 : 120
            }}>
              <Typography 
                variant="h6" 
                gutterBottom
                sx={{ fontSize: isMobile ? '1.1rem' : '1.25rem' }}
              >
                Script Generator
              </Typography>
              <Typography 
                variant="body2" 
                color="text.secondary" 
                align="center" 
                sx={{ 
                  mt: 2,
                  fontSize: isMobile ? '0.875rem' : '1rem'
                }}
              >
                AI guidance will appear here based on your uploaded script
              </Typography>
            </Box>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};

export default App;
