interface ConversationContext {
  text: string;
  timestamp: number;
}

interface ExternalGPTConfig {
  apiKey?: string;
  endpoint?: string;
  model?: string;
}

class AIService {
  private conversationHistory: ConversationContext[] = [];
  private externalGPTConfig: ExternalGPTConfig = {};

  async configureExternalGPT(config: ExternalGPTConfig) {
    this.externalGPTConfig = config;
    console.log('External GPT configured:', config);
  }

  async processInput(text: string): Promise<string> {
    // Add to conversation history
    this.conversationHistory.push({
      text,
      timestamp: Date.now()
    });

    if (this.externalGPTConfig.endpoint) {
      try {
        const response = await fetch(this.externalGPTConfig.endpoint, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            ...(this.externalGPTConfig.apiKey && {
              'Authorization': `Bearer ${this.externalGPTConfig.apiKey}`
            })
          },
          body: JSON.stringify({
            model: this.externalGPTConfig.model,
            messages: [
              ...this.conversationHistory.map(ctx => ({
                role: "user",
                content: ctx.text
              }))
            ]
          })
        });

        if (!response.ok) {
          throw new Error('Failed to get response from external GPT');
        }

        const data = await response.json();
        return data.choices?.[0]?.message?.content || 'No response from external GPT';
      } catch (error) {
        console.error('Error calling external GPT:', error);
        return 'Error: Failed to get response from external GPT';
      }
    }

    // Fallback response if no external GPT is configured
    return `Please configure external GPT first. Current input: ${text}`;
  }

  clearContext() {
    this.conversationHistory = [];
  }

  getConversationHistory(): ConversationContext[] {
    return [...this.conversationHistory];
  }

  isExternalGPTConfigured(): boolean {
    return Boolean(this.externalGPTConfig.endpoint);
  }
}

export const aiService = new AIService();
export default aiService;
