import React, { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Box,
  Alert,
  IconButton,
  Typography
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import aiService from '../services/aiService';

interface GPTConfigProps {
  open: boolean;
  onClose: () => void;
}

const GPTConfig: React.FC<GPTConfigProps> = ({ open, onClose }) => {
  const [endpoint, setEndpoint] = useState('');
  const [apiKey, setApiKey] = useState('');
  const [model, setModel] = useState('');
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState(false);

  const handleSave = async () => {
    try {
      if (!endpoint) {
        setError('Endpoint URL is required');
        return;
      }

      await aiService.configureExternalGPT({
        endpoint,
        apiKey,
        model: model || undefined
      });

      setSuccess(true);
      setTimeout(() => {
        onClose();
      }, 1500);
    } catch (error) {
      setError('Failed to configure external GPT');
    }
  };

  return (
    <Dialog 
      open={open} 
      onClose={onClose}
      maxWidth="sm"
      fullWidth
      PaperProps={{
        sx: { borderRadius: '12px' }
      }}
    >
      <DialogTitle>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Typography variant="h6">Configure External GPT</Typography>
          <IconButton onClick={onClose}>
            <CloseIcon />
          </IconButton>
        </Box>
      </DialogTitle>
      <DialogContent>
        <Box sx={{ mt: 2 }}>
          <TextField
            fullWidth
            label="GPT Endpoint URL"
            value={endpoint}
            onChange={(e) => setEndpoint(e.target.value)}
            placeholder="https://api.example.com/v1/chat/completions"
            margin="normal"
            required
            helperText="Enter the API endpoint for your custom GPT model"
          />
          <TextField
            fullWidth
            label="API Key"
            value={apiKey}
            onChange={(e) => setApiKey(e.target.value)}
            type="password"
            margin="normal"
            helperText="Your GPT API authentication key"
          />
          <TextField
            fullWidth
            label="Model Name"
            value={model}
            onChange={(e) => setModel(e.target.value)}
            placeholder="gpt-4"
            margin="normal"
            helperText="Specify which GPT model to use (e.g., gpt-4, gpt-3.5-turbo)"
          />
          {error && (
            <Alert severity="error" sx={{ mt: 2 }}>
              {error}
            </Alert>
          )}
          {success && (
            <Alert severity="success" sx={{ mt: 2 }}>
              External GPT configured successfully! The system will now use your custom GPT for conversations.
            </Alert>
          )}
        </Box>
      </DialogContent>
      <DialogActions sx={{ p: 3 }}>
        <Button onClick={onClose} color="inherit">
          Cancel
        </Button>
        <Button 
          onClick={handleSave}
          variant="contained"
          sx={{
            background: 'linear-gradient(45deg, #3498db, #2980b9)',
            '&:hover': {
              background: 'linear-gradient(45deg, #2980b9, #2c3e50)'
            }
          }}
        >
          Save Configuration
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default GPTConfig;
