import React from 'react';
import { Box, Typography } from '@mui/material';
import SmartToyIcon from '@mui/icons-material/SmartToy';

interface LogoProps {
  onClick?: () => void;
}

const Logo: React.FC<LogoProps> = ({ onClick }) => {
  return (
    <Box 
      sx={{ 
        display: 'flex', 
        alignItems: 'center', 
        gap: 2, 
        cursor: onClick ? 'pointer' : 'default'
      }}
      onClick={onClick}
    >
      <SmartToyIcon 
        sx={{ 
          fontSize: '2.2rem',
          color: '#3498db',
          filter: 'drop-shadow(0 2px 2px rgba(0,0,0,0.1))'
        }} 
      />
      <Typography 
        variant="h5" 
        sx={{ 
          color: '#3498db',
          fontWeight: 600,
          userSelect: 'none',
          letterSpacing: '0.5px',
          textShadow: '0 1px 2px rgba(0,0,0,0.1)'
        }}
      >
        Sales Assistant AI
      </Typography>
    </Box>
  );
};

export default Logo;
