import React, { useState } from 'react';
import { Box, Button, Card, Typography, IconButton, Collapse, useTheme, useMediaQuery } from '@mui/material';
import DashboardIcon from '@mui/icons-material/Dashboard';
import SettingsIcon from '@mui/icons-material/Settings';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import GPTConfig from './GPTConfig';
import Logo from './Logo';
import { DifficultyLevel, CategoryType, SubScenario } from '../types/practice';
import { categories } from '../services/practiceService';

interface PracticeModeProps {
  onExitPracticeMode?: () => void;
}

const PracticeMode: React.FC<PracticeModeProps> = ({ onExitPracticeMode }) => {
  const [selectedCategory, setSelectedCategory] = useState<CategoryType | null>(null);
  const [selectedScenario, setSelectedScenario] = useState<SubScenario | null>(null);
  const [selectedDifficulty, setSelectedDifficulty] = useState<DifficultyLevel | null>(null);
  const [expandedCategory, setExpandedCategory] = useState<string | null>(null);
  const [showGPTConfig, setShowGPTConfig] = useState(false);

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const handleCategoryClick = (category: CategoryType) => {
    setSelectedCategory(category);
  };

  const handleExpandClick = (categoryId: string, event: React.MouseEvent) => {
    event.stopPropagation();
    setExpandedCategory(expandedCategory === categoryId ? null : categoryId);
  };

  const handleScenarioSelect = (scenario: SubScenario) => {
    setSelectedScenario(scenario);
  };

  const handleDifficultySelect = (difficulty: DifficultyLevel, scenario: SubScenario) => {
    setSelectedDifficulty(difficulty);
    setSelectedScenario(scenario);
  };

  const handleStartPractice = () => {
    if (selectedScenario && selectedDifficulty) {
      // TODO: Implement practice session logic
      console.log('Starting practice session:', { selectedScenario, selectedDifficulty });
    }
  };

  const handleConfigureGPT = () => {
    setShowGPTConfig(true);
  };

  const handleCloseDialog = () => {
    setShowGPTConfig(false);
  };

  return (
    <Box sx={{ p: isMobile ? 2 : 3 }}>
      {/* Header */}
      <Box sx={{ 
        display: 'flex', 
        flexDirection: isMobile ? 'column' : 'row',
        justifyContent: 'space-between', 
        alignItems: isMobile ? 'stretch' : 'center',
        mb: 4,
        pb: 2,
        gap: isMobile ? 2 : 0,
        borderBottom: '1px solid rgba(0,0,0,0.1)'
      }}>
        <Logo onClick={onExitPracticeMode} />
        <Box sx={{ 
          display: 'flex', 
          gap: 2,
          flexDirection: isMobile ? 'column' : 'row'
        }}>
          <Button 
            variant="outlined" 
            color="primary"
            fullWidth={isMobile}
            startIcon={<SettingsIcon />}
            onClick={handleConfigureGPT}
            sx={{ 
              borderRadius: '8px',
              textTransform: 'none',
              fontWeight: 600
            }}
          >
            Configure GPT
          </Button>
          <Button 
            variant="contained" 
            color="primary"
            fullWidth={isMobile}
            startIcon={<DashboardIcon />}
            sx={{ 
              borderRadius: '8px',
              textTransform: 'none',
              fontWeight: 600,
              background: 'linear-gradient(45deg, #3498db, #2980b9)',
              '&:hover': {
                background: 'linear-gradient(45deg, #2980b9, #2c3e50)'
              }
            }}
          >
            Dashboard
          </Button>
        </Box>
      </Box>

      {/* Main Content */}
      <Box sx={{ maxWidth: 1200, margin: '0 auto' }}>
        <Typography 
          variant="h4" 
          sx={{ 
            mb: 3,
            fontWeight: 700,
            color: '#2c3e50',
            fontSize: isMobile ? '1.75rem' : '2.125rem'
          }}
        >
          Select Practice Category
        </Typography>

        <Box sx={{ 
          display: 'grid', 
          gridTemplateColumns: isMobile ? '1fr' : 'repeat(2, 1fr)', 
          gap: 3 
        }}>
          {categories.map((category) => (
            <Box key={category.id}>
              <Card 
                sx={{ 
                  p: 3,
                  cursor: 'pointer',
                  border: selectedCategory?.id === category.id ? '2px solid #3498db' : '1px solid #eee',
                  borderRadius: '12px',
                  transition: 'all 0.3s ease',
                  '&:hover': {
                    transform: 'translateY(-2px)',
                    boxShadow: '0 4px 12px rgba(0,0,0,0.1)'
                  }
                }}
                onClick={() => handleCategoryClick(category)}
              >
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                  <Typography 
                    variant="h6" 
                    sx={{ 
                      fontWeight: 600,
                      color: '#2c3e50',
                      fontSize: isMobile ? '1.1rem' : '1.25rem'
                    }}
                  >
                    {category.name}
                  </Typography>
                  {category.subScenarios && (
                    <IconButton 
                      size="small"
                      onClick={(e) => handleExpandClick(category.id, e)}
                    >
                      {expandedCategory === category.id ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                    </IconButton>
                  )}
                </Box>
                <Typography 
                  variant="body2" 
                  color="text.secondary" 
                  sx={{ mb: 2 }}
                >
                  {category.description}
                </Typography>
              </Card>

              <Collapse in={expandedCategory === category.id} timeout="auto" unmountOnExit>
                <Box sx={{ mt: 2, pl: isMobile ? 0 : 2 }}>
                  {category.subScenarios?.map((scenario) => (
                    <Card 
                      key={scenario.id}
                      sx={{ 
                        p: 2,
                        mb: 2,
                        cursor: 'pointer',
                        border: selectedScenario?.id === scenario.id ? '2px solid #3498db' : '1px solid #eee',
                        borderRadius: '8px',
                        transition: 'all 0.3s ease',
                        '&:hover': {
                          transform: isMobile ? 'translateY(-2px)' : 'translateX(8px)',
                          boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
                        }
                      }}
                      onClick={() => handleScenarioSelect(scenario)}
                    >
                      <Typography 
                        variant="subtitle1" 
                        sx={{ 
                          fontWeight: 600,
                          color: '#2c3e50',
                          mb: 1,
                          fontSize: isMobile ? '1rem' : '1.1rem'
                        }}
                      >
                        {scenario.name}
                      </Typography>
                      <Typography 
                        variant="body2" 
                        color="text.secondary"
                        sx={{ mb: 2 }}
                      >
                        {scenario.description}
                      </Typography>
                      
                      <Box sx={{ 
                        display: 'flex', 
                        gap: 1,
                        flexDirection: isMobile ? 'column' : 'row'
                      }}>
                        <Button
                          variant={selectedDifficulty === 'beginner' && selectedScenario?.id === scenario.id ? 'contained' : 'outlined'}
                          size="small"
                          fullWidth={isMobile}
                          onClick={(e) => {
                            e.stopPropagation();
                            handleDifficultySelect('beginner', scenario);
                          }}
                          sx={{
                            flex: 1,
                            textTransform: 'none',
                            backgroundColor: selectedDifficulty === 'beginner' && selectedScenario?.id === scenario.id ? '#27ae60' : 'transparent',
                            borderColor: '#27ae60',
                            color: selectedDifficulty === 'beginner' && selectedScenario?.id === scenario.id ? 'white' : '#27ae60',
                            '&:hover': {
                              backgroundColor: '#27ae60',
                              color: 'white'
                            }
                          }}
                        >
                          Beginner
                        </Button>
                        <Button
                          variant={selectedDifficulty === 'advanced' && selectedScenario?.id === scenario.id ? 'contained' : 'outlined'}
                          size="small"
                          fullWidth={isMobile}
                          onClick={(e) => {
                            e.stopPropagation();
                            handleDifficultySelect('advanced', scenario);
                          }}
                          sx={{
                            flex: 1,
                            textTransform: 'none',
                            backgroundColor: selectedDifficulty === 'advanced' && selectedScenario?.id === scenario.id ? '#e74c3c' : 'transparent',
                            borderColor: '#e74c3c',
                            color: selectedDifficulty === 'advanced' && selectedScenario?.id === scenario.id ? 'white' : '#e74c3c',
                            '&:hover': {
                              backgroundColor: '#e74c3c',
                              color: 'white'
                            }
                          }}
                        >
                          Advanced
                        </Button>
                      </Box>
                    </Card>
                  ))}
                </Box>
              </Collapse>
            </Box>
          ))}
        </Box>

        <Box sx={{ mt: 4, textAlign: 'center' }}>
          <Button
            variant="contained"
            color="primary"
            size="large"
            fullWidth={isMobile}
            disabled={!selectedScenario || !selectedDifficulty}
            onClick={handleStartPractice}
            sx={{ 
              minWidth: isMobile ? '100%' : 240,
              height: 48,
              fontSize: '1.1rem',
              fontWeight: 600,
              borderRadius: '24px',
              textTransform: 'none',
              background: selectedScenario && selectedDifficulty
                ? 'linear-gradient(45deg, #3498db, #2980b9)'
                : 'rgba(0,0,0,0.12)',
              '&:hover': {
                background: 'linear-gradient(45deg, #2980b9, #2c3e50)'
              }
            }}
          >
            Start Practice Session
          </Button>
        </Box>
      </Box>

      {/* GPT Config Dialog */}
      <GPTConfig 
        open={showGPTConfig}
        onClose={handleCloseDialog}
      />
    </Box>
  );
};

export default PracticeMode;
